import React, { Component } from "react";

export default class Carousel extends Component {
  constructor(props) {
    super(props)
    this.state = {
      activeEl: null
    }

    this.onHover = this.onHover.bind(this);
  }

  onHover(event) {
    const {name, value} = event.target
    this.setState({[name]: value})
  }

  render() {
    const {activeEl} = this.state
    const {data} = this.props.data

    return (
      <div>
        <div className="flex flex-wrap py-12 mt-32 items-end">
          {this.props.data.map(c => (
            <div className="w-1/5 text-center first:opacity-50 last:opacity-50">
              <img className="inline" style={{maxWidth: "100px", maxHeight:"300px"}} src={c.img} onHover={this.onHover}/>
            </div>
          ))}
        </div>
        <div className="w-full my-24 text-center justify-center text-sm">
          <p className="max-w-md mx-auto">{activeEl ? data[activeEl].description : this.props.defaultDescription}</p>
        </div>
      </div>
    )
  }
}

Carousel.defaultProps = {
  defaultDescription: "asdfasdf default description"
}
